.typeContainer{
    height: 47px;
    display: flex;
    justify-content: flex-end;
    padding-top: 17px;
    padding-bottom: 14px;
    line-height: 1 !important;
    }
    
    .typeContainer .type{
    height: 22px !important;
    margin-right: 8px;
    border-radius: 4px;
    }
    
    .typeContainer .type .text{
    padding: 1px 8px;
    text-transform: uppercase;
    vertical-align: middle;
    color: white;
    }
    
    .typeContainer .type:last-child{
    margin-right: 24px;
    }
    
    .typeContainer .type.normal {
    background-color: #A8A878;
    border: 1px solid #6D6D4E;
    }
    
    .typeContainer .type.fire {
    background-color: #F08030;
    border: 1px solid #9C531F
    }
    
    .typeContainer .type.fighting {
    background: #C03028;
    border: 1px solid #7D1F1A;
    }
    
    .typeContainer .type.water {
    background: #6890F0;
    border: 1px solid #445E9C;
    }
    
    .typeContainer .type.flying {
    background: #A890F0;
    border: 1px solid #6D5E9C;
    }
    
    .typeContainer .type.grass {
    background: #78C850;
    border: 1px solid #4E8234;
    }
    
    .typeContainer .type.poison {
    background: #A040A0;
    border: 1px solid #682A68;
    }
    
    .typeContainer .type.electric {
    background: #F8D030;
    border: 1px solid #A1871F;
    }
    
    .typeContainer .type.ground {
    background: #E0C068;
    border: 1px solid #927D44;
    }
    
    .typeContainer .type.psychic {
    background: #F85888;
    border: 1px solid #A13959;
    }
    
    .typeContainer .type.rock {
    background: #B8A038;
    border: 1px solid #786824;
    }
    
    .typeContainer .type.ice {
    background: #98D8D8;
    border: 1px solid #638D8D;
    }
    
    .typeContainer .type.bug {
    background: #A8B820;
    border: 1px solid #6D7815;
    }
    
    .typeContainer .type.dragon {
    background: #7038F8;
    border: 1px solid #4924A1;
    }
    
    .typeContainer .type.ghost {
    background: #705898;
    border: 1px solid #493963;
    }
    
    .typeContainer .type.dark {
    background: #705848;
    border: 1px solid #49392F;
    }
    
    .typeContainer .type.steel {
    background: #B8B8D0;
    border: 1px solid #787887;
    }
    
    .typeContainer .type.fairy {
    background: #EE99AC;
    border: 1px solid #9B6470;
    }
    
    .rightArrow, .leftArrow, .searchIcon, .closeIcon{
    font-size: 25px;
    color: white;
    position: relative;
    }
    
    .leftArrow, .rightArrow{
    height: 64px;
    width: 64px;
    border-radius: 64px;
    margin: 64px;
    background-color: var(--primary-color);
    filter: brightness(93%);
    transition-duration: .25s; 
    }
    
    .leftArrow:hover, .rightArrow:hover{
    color: var(--primary-color);
    background-color: white;
    filter: brightness(100%);
    }
    
    .rightArrow svg,  .leftArrow svg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }